@use "global" as *;

.btn{
  position: relative;
  overflow: hidden;
  text-decoration: none;
  display: inline-block;
  border: rem(1) solid $white;
  padding: rem(10) rem(114);
  text-align: center;
  outline: none;
  transition: ease .3s;
  background-color: #333;
  max-width: rem(335);
  width: 100%;

  @include mq(md) {
    padding: 15px 114px;
    max-width: rem(360);
    width: 100%;
  }
}

.btn span {
  position: relative;
  z-index: 3;
  color: $white;
  white-space: nowrap;
  font-size: rem(20);
  line-height: 1.4;

  @include mq(md) {
    line-height: 1;
    font-size: rem(18);
}
}

.btn:hover span{
color:#333;
}

.bgleft::before {
 content: '';
 position: absolute;
 top: 0;
 left: 0;
 z-index: 2;
 background: $white;
 width: 100%;
 height: 100%;
 transition: transform .6s cubic-bezier(0.8, 0, 0.2, 1) 0s;
 transform: scale(0, 1);
transform-origin: right top;
}

.bgleft:hover:before{
transform-origin:left top;
transform:scale(1, 1);
}